import React, { useState, useCallback, useRef } from 'react';
import { X, ChevronLeft, ChevronRight } from 'lucide-react';
import { StoryContent } from './Stories';
import { hapticSelection } from '@/utils/telegramUtils';

interface ImageSwiperProps {
  story: StoryContent;
  onClose: () => void;
}

const ImageSwiper: React.FC<ImageSwiperProps> = ({ 
  story, 
  onClose
}) => {
  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  
  // Swipe state
  const [isDragging, setIsDragging] = useState(false);
  const [translateX, setTranslateX] = useState(0);
  const touchStartRef = useRef({ x: 0, y: 0 });
  const swiperRef = useRef<HTMLDivElement>(null);

  // Handle navigation
  const goToPrevious = useCallback(() => {
    hapticSelection();
    if (currentImageIndex > 0) {
      setCurrentImageIndex(prev => prev - 1);
    }
  }, [currentImageIndex]);

  const goToNext = useCallback(() => {
    hapticSelection();
    if (currentImageIndex < story.images.length - 1) {
      setCurrentImageIndex(prev => prev + 1);
    }
  }, [currentImageIndex, story.images.length]);

  // Touch handlers for swipe navigation
  const handleTouchStart = (e: React.TouchEvent) => {
    e.preventDefault();
    
    touchStartRef.current = { 
      x: e.touches[0].clientX,
      y: e.touches[0].clientY
    };
  };
  
  const handleTouchMove = (e: React.TouchEvent) => {
    e.preventDefault();
    
    const touchX = e.touches[0].clientX;
    const touchY = e.touches[0].clientY;
    const deltaX = touchX - touchStartRef.current.x;
    const deltaY = Math.abs(touchY - touchStartRef.current.y);
    
    // Only handle horizontal swipes (deltaX > deltaY means more horizontal than vertical)
    const isHorizontalMovement = Math.abs(deltaX) > deltaY;
    const hasSignificantMovement = Math.abs(deltaX) > 20;
    
    if (isHorizontalMovement && hasSignificantMovement) {
      if (!isDragging) {
        setIsDragging(true);
      }
      
      // Limit the drag distance
      const maxDrag = 100;
      const limitedDeltaX = Math.min(Math.max(deltaX, -maxDrag), maxDrag);
      setTranslateX(limitedDeltaX);
    }
  };
  
  const handleTouchEnd = (e: React.TouchEvent) => {
    e.preventDefault();
    
    if (isDragging) {
      const swipeThreshold = 50;
      
      if (translateX > swipeThreshold && currentImageIndex > 0) {
        // Swiped right - go to previous
        goToPrevious();
      } else if (translateX < -swipeThreshold && currentImageIndex < story.images.length - 1) {
        // Swiped left - go to next
        goToNext();
      }
      
      // Reset position
      setTranslateX(0);
      setIsDragging(false);
    }
  };

  return (
    <div className="fixed inset-0 bg-black/90 z-50 flex items-center justify-center p-4">
      {/* Background overlay */}
      <div 
        className="absolute inset-0" 
        onClick={onClose}
      />
      
      {/* Image swiper container */}
      <div 
        ref={swiperRef}
        className="relative bg-white dark:bg-gray-800 rounded-2xl shadow-2xl max-w-md w-full max-h-[80vh] overflow-hidden"
        onTouchStart={handleTouchStart}
        onTouchMove={handleTouchMove}
        onTouchEnd={handleTouchEnd}
      >
        {/* Close button */}
        <button 
          onClick={() => {
            hapticSelection();
            onClose();
          }}
          className="absolute top-4 right-4 z-20 text-gray-600 dark:text-gray-300 p-2 bg-white/80 dark:bg-gray-800/80 rounded-full hover:bg-white dark:hover:bg-gray-700 transition-colors"
        >
          <X size={20} />
        </button>

        {/* Header with title */}
        <div className="absolute top-0 left-0 right-0 z-10 bg-gradient-to-b from-black/50 to-transparent p-4">
          <h3 className="text-white font-semibold text-lg">{story.title}</h3>
        </div>

        {/* Image container */}
        <div className="relative aspect-[3/4] overflow-hidden">
          <div 
            className="flex transition-transform duration-300 ease-out h-full"
            style={{ 
              transform: `translateX(calc(-${currentImageIndex * 100}% + ${translateX}px))` 
            }}
          >
            {story.images.map((image, index) => (
              <div 
                key={index}
                className="min-w-full h-full"
              >
                <img 
                  src={image} 
                  alt={`${story.title} - ${index + 1}`}
                  className="w-full h-full object-cover"
                  draggable={false}
                />
              </div>
            ))}
          </div>

          {/* Navigation buttons */}
          {currentImageIndex > 0 && (
            <button
              onClick={goToPrevious}
              className="absolute left-4 top-1/2 -translate-y-1/2 z-10 p-2 bg-black/50 text-white rounded-full hover:bg-black/70 transition-colors"
            >
              <ChevronLeft size={24} />
            </button>
          )}
          
          {currentImageIndex < story.images.length - 1 && (
            <button
              onClick={goToNext}
              className="absolute right-4 top-1/2 -translate-y-1/2 z-10 p-2 bg-black/50 text-white rounded-full hover:bg-black/70 transition-colors"
            >
              <ChevronRight size={24} />
            </button>
          )}
        </div>

        {/* Bottom info and dots */}
        <div className="p-4 bg-white dark:bg-gray-800">
          {/* Dot indicators */}
          <div className="flex justify-center gap-2 mb-2">
            {story.images.map((_, index) => (
              <button
                key={index}
                onClick={() => setCurrentImageIndex(index)}
                className={`w-2 h-2 rounded-full transition-colors ${
                  index === currentImageIndex 
                    ? 'bg-blue-500' 
                    : 'bg-gray-300 dark:bg-gray-600'
                }`}
              />
            ))}
          </div>
          
          {/* Image counter */}
          <div className="text-center text-sm text-gray-600 dark:text-gray-400">
            {currentImageIndex + 1} / {story.images.length}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ImageSwiper; 